export default {
	/* navigateTo跳转 */
	/* 进入页面则发起请求 one/two=>detail*/
	onLoad(option) {
		console.log('detail',option.id)
		uni.setStorage({
			key:'id',
			data:option.id,
			success(res) {
				console.log('success',res)
			}
		})	
		
	},
	data() {
		return {
			foodlist:[],
			flag:true,
			isshow:false
		};
	},
	methods:{
		/* 购买 */
		buy(id){
			console.log(id)
			// this.flag=false
			
		},
		shopcar(){
			this.isshow=!this.isshow
		},
		addbtn(){
			this.isshow=!this.isshow
		}
	}
	,
	created() {
		let id;
		let self=this
		uni.getStorage({
			key:'id',
			success: function (res) {
			    console.log('res.data',res.data);
				id=res.data
			}
		})
		uni.request({
			url:'http://localhost:10086/detail',
			data:{
				id:String(id)
			},
			success(res) {
				console.log('detail-data',res.data.data)
				self.foodlist=res.data.data
			}
		})
	}
	
}
